.form-container {
  width: 100%;
  max-width: 600px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  background-color: #f5f5f5;
  border-radius: 10px;
  box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.1);
}

.form-title {
  font-size: 24px;
  font-weight: bold;
  margin-bottom: 20px;
}

.form-input {
  padding: 10px;
  border: none;
  border-radius: 5px;
  box-shadow: 1px 1px 5px #ddd;
  font-size: 16px;
  margin-bottom: 10px;
  width: 100%;
  max-width: 400px;
}

.form-label {
  font-size: 16px;
  font-weight: bold;
  margin-bottom: 10px;
  text-align: left;
  width: 100%;
  max-width: 400px;
}

.submit-btn {
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  background-color: #0077b6;
  color: #fff;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.2s;
  margin-top: 20px;
}

.submit-btn:hover {
  background-color: #005e8c;
}

.table-container {
  display: flex;
  justify-content: center;
}

.table {
  width: 80%;
  margin-top: 20px;
  border-collapse: collapse;
  font-size: 16px;
  text-align: center;
  box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
  overflow: hidden;
  margin-left: 10%;
}

.table thead {
  background-color: #0077b6;
  color: #fff;
}

.table th,
.table td {
  padding: 15px;
  border-bottom: 1px solid #ddd;
}

.table th {
  font-size: 18px;
}

.table tbody tr:nth-child(even) {
  background-color: #f2f2f2;
}

.table tbody tr:hover {
  background-color: #e8f4f8;
}
